@import url("https://fonts.googleapis.com/css2?family=Song+Myung&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&display=swap");

body {
  font-family: "Nanum Pen Script";
  line-height: 1.5;
  font-weight: bolder;
  margin-left: 50px;
  font-size: 1.5em; 
  margin:0 auto;
  padding: 0;
  background-image: url("../public/image/background.jpg"); 
  background-size: cover; 
  background-attachment: fixed; 
}

header {
  text-align: center;
  font-size: 2em; 
  margin-bottom: 10px; 
}

#index {
  text-align: center;
  font-size: 4em; 
  margin-top:300px;
  margin-left: 100px; 
  margin-bottom: 350px;
}

.container {
  display: grid;
  
}

nav {
  font-family: "Nanum Pen Script";
  position: fixed;
  top: 0;
  left: 10px;
  font-size: 1.2em;
  
  padding: 10px;
  z-index: 1000;
}


main {
  font-family: "Nanum Pen Script";
  margin-left: 20px;
  margin-top: 90px;
  font-size: 1em; 
}

a:link,
a:visited {
  background-color: white;
  padding: 10px 20px;
  text-decoration: none;
  display: inline-block;
  width: 330px;
  font-size: 1em; 
  border-radius: 10px;
  border-color: black;
}

a:hover,
a:active {
  background-color: gray;
  color: white;
}

button {
  font-family: "Nanum Pen Script";
  background-color: gray;
  color: white;
  padding: 10px 20px;
  margin-top: 100px;
  margin-left:30px;
  font-size: 1.5em;
  cursor: pointer;
  transition-duration: 1s;
}

button:hover {
  background-color: green;
  color: white;
}

.dropdown {
  font-family: "Nanum Pen Script";
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.dropdown:hover .dropdown-content {
  display: block;
}

h2{
  font-size: 50px;
  font-family: 'Arial', sans-serif;;
}
