.selfIntro {
  display: flex;
  align-items: center;
  justify-content: space-between; 
  margin: 20px;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #f8f8f8;
}

img {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
  margin-top: 10px;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

table, th, td {
  border: 1px solid #ddd;
}

th, td {
  padding: 12px;
  text-align: left;
}

th {
  background-color: #f2f2f2;
}

h3 {
  color: blue;
}

.division {
  margin-left: 100px;
}

p {
  border: 7px dashed blue;
}

#selfIntro .image-container {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

#selfIntro img {
  width: 200px; 
  height: auto;
  border-radius: 8px;
  margin-right: 30px; 
}

 hr {
  border: 2px solid green;
  margin: 20px 0; 
}