
.project{
  width:480px;
  height:320px;
}

fieldset {
  width: 1500px;
  margin-left: 50px;
  font-size: 1.5em;
}

.contest{
  width:120px;
  height:240px;
}

.project-container {
  text-align: center;
}

.project-slide {
  display: flex;
  transition: transform 0.5s ease-in-out;
}

.project-content {
  flex: 0 0 100%;
  box-sizing: border-box;
}

.project {
  max-width: 100%;
  height: auto;
}

.button-container {
  margin-top: 10px;
}

.button-container button {
  margin: 0 10px;
  cursor: pointer;
}

li{
  list-style: none;
}

.project-content img {
  height: 200px; /* Adjust the height according to your design */
  width: auto;   /* Maintain the aspect ratio */
}